const SearchForm = props => (
  <form>
    <input value={props.searchVal} placeholder="Search text here" onChange={props.handleChange} />
    <button onClick={props.selectRecipes}>Search</button>
  </form>
);

const DirectoryView = props => (
  <div className="directory-view">
    <SearchForm
      searchVal={props.searchVal}
      handleChange={props.handleChange}
      selectRecipes={props.selectRecipes}
    />
    {props.recipes.map(recipe => (
      <RecipeCard name={recipe.name} handleClick={() => props.handleClick(recipe)} />
    ))}

  </div>
);

const RecipeCard = props => (
  <div className="recipe-card" onClick={props.handleClick}>
    {props.name}
  </div>
);

