  selectRecipes = event => {
    event.preventDefault();
    const matchingRecipes = this.state.recipes.filter(
      recipe => recipe.name.toLowerCase().includes(this.state.searchVal.toLowerCase())
    );
    this.setState({ selectedRecipes: matchingRecipes })
  }

  handleChange = event => {
    this.setState({ searchVal: event.target.value });
  };

  handleClick = recipe => {
    this.setState({ selectedRecipe: recipe });
  };

