import React from "react";

const DetailView = props => (
  <div className="detail-view">
    <div className="recipe-name">
      <span className="recipe-icon">{props.recipe.icon}</span>
      {' '}
      {props.recipe.name}
    </div>
    <div className="wrapper">
      <div>
        <div className="title-ingredients">Ingredients</div>
        {props.recipe.ingredients.map(ingredient => (
          <div className="recipe-ingredient">{ingredient}</div>
        ))}
      </div>
      <div>
        <div className="title-instructions">Instructions</div>
        {props.recipe.instructions.map(instruction => (
          <div className="recipe-instruction">{instruction}</div>)
        )}
      </div>
    </div>
  </div>)

export default DetailView;